html {
  box-sizing: border-box;
}

.breed-select {
  width: 250px;
  height: 30px;
  margin: 50px;
  font-weight: 600;
}

.cat-info {
  max-height: 400px;
  display: flex;
}

.cat-info > img {
  max-height: 400px;
  border-radius: 30px;
}

.cat-item {
  max-width: 300px;
  margin-left: 20px;
  padding: 20px;
}

.loader.is-hidden, .error.is-hidden {
  display: none;
}

.error {
  font-size: 30px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #00f;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.example {
  color: #2f2f37;
  max-width: 960px;
  background-color: #fff;
  padding: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.example code {
  color: #fff;
  background-color: #ff6b0a;
  border-radius: 2px;
  padding: 2px 6px;
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
}

.example .background {
  height: 240px;
  background-image: url("logo.268195b3.png");
  background-position: center;
  background-size: cover;
}

